export const getCategoryColor = (category: string): string => {
  const categoryColors: { [key: string]: string } = {
    acquisition: theme.colors.primary,
    tracking: theme.colors.primary,
    analytics: theme.colors.secondary,
    optimization: theme.colors.secondary,
    targeting: theme.colors.secondary,
    management: theme.colors.secondary,
    automation: theme.colors.secondary,
    sales: theme.colors.inverse,
    nurture: theme.colors.inverse,
    fulfillment: theme.colors.inverse,
    operations: theme.colors.inverse
  };
  
  return categoryColors[category] || theme.colors.neutral;
};

export const getCategoryGlow = (category: string): string => {
  const categoryGlows: { [key: string]: string } = {
    acquisition: theme.colors.primaryGlow,
    tracking: theme.colors.primaryGlow,
    analytics: theme.colors.secondaryGlow,
    optimization: theme.colors.secondaryGlow,
    targeting: theme.colors.secondaryGlow,
    management: theme.colors.secondaryGlow,
    automation: theme.colors.secondaryGlow,
    sales: 'rgba(248, 243, 222, 0.4)',
    nurture: 'rgba(248, 243, 222, 0.4)',
    fulfillment: 'rgba(248, 243, 222, 0.4)',
    operations: 'rgba(248, 243, 222, 0.4)'
  };
  
  return categoryGlows[category] || 'rgba(10, 10, 13, 0.4)';
};