import { useRef, useEffect, useMemo } from 'react';
import * as d3 from 'd3';
import type { SimulationNode, SimulationLink, TouchPoint, NetworkDimensions } from '../components/types';
import { theme } from '../styles/theme';

interface UseForceSimulationProps {
  nodes: TouchPoint[];
  dimensions: NetworkDimensions;
  onTick: (nodes: SimulationNode[], links: SimulationLink[]) => void;
}

export const useForceSimulation = ({ 
  nodes, 
  dimensions, 
  onTick 
}: UseForceSimulationProps) => {
  const simulationRef = useRef<d3.Simulation<SimulationNode, SimulationLink> | null>(null);
  const animationFrameRef = useRef<number | null>(null);
  
  const simulationNodes: SimulationNode[] = useMemo(() => 
    nodes.map(node => ({ ...node })), 
    [nodes]
  );

  const simulationLinks: SimulationLink[] = useMemo(() => {
    const links: SimulationLink[] = [];
    nodes.forEach(node => {
      node.connections.forEach(connectionId => {
        const targetNode = nodes.find(n => n.id === connectionId);
        if (targetNode) {
          links.push({
            source: node.id,
            target: connectionId
          });
        }
      });
    });
    return links;
  }, [nodes]);

  useEffect(() => {
    if (!dimensions.width || !dimensions.height) return;

    // Create or update simulation
    if (!simulationRef.current) {
      simulationRef.current = d3.forceSimulation<SimulationNode>(simulationNodes);
    } else {
      simulationRef.current.nodes(simulationNodes);
    }

    const simulation = simulationRef.current;

    // Configure forces
    simulation
      .force('link', d3.forceLink<SimulationNode, SimulationLink>(simulationLinks)
        .id(d => d.id)
        .distance(theme.physics.linkDistance)
        .strength(theme.physics.linkStrength)
      )
      .force('charge', d3.forceManyBody()
        .strength(theme.physics.chargeStrength)
      )
      .force('center', d3.forceCenter(
        dimensions.width / 2, 
        dimensions.height / 2
      ))
      .force('collision', d3.forceCollide()
        .radius(theme.physics.collideRadius)
      )
      .alpha(theme.physics.alpha)
      .alphaDecay(theme.physics.alphaDecay)
      .velocityDecay(theme.physics.velocityDecay);

    // Handle tick events with requestAnimationFrame for smooth updates
    const handleTick = () => {
      // Cancel any pending animation frame
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
      
      // Schedule update for next frame
      animationFrameRef.current = requestAnimationFrame(() => {
        onTick(simulationNodes, simulationLinks);
        animationFrameRef.current = null;
      });
    };

    simulation.on('tick', handleTick);

    // Restart simulation
    simulation.restart();

    return () => {
      simulation.on('tick', null);
      if (animationFrameRef.current) {
        cancelAnimationFrame(animationFrameRef.current);
      }
    };
  }, [simulationNodes, simulationLinks, dimensions, onTick]);

  const updateNodePosition = (nodeId: string, x: number, y: number) => {
    const node = simulationNodes.find(n => n.id === nodeId);
    if (node && simulationRef.current) {
      node.fx = x;
      node.fy = y;
      // Use lower alpha for smoother drag operations
      simulationRef.current.alpha(0.1).restart();
    }
  };

  const releaseNode = (nodeId: string) => {
    const node = simulationNodes.find(n => n.id === nodeId);
    if (node && simulationRef.current) {
      node.fx = null;
      node.fy = null;
      // Gentle restart after release
      simulationRef.current.alpha(0.05).restart();
    }
  };

  const reheatSimulation = () => {
    if (simulationRef.current) {
      simulationRef.current.alpha(0.3).restart();
    }
  };

  useEffect(() => {
    return () => {
      if (simulationRef.current) {
        simulationRef.current.stop();
      }
    };
  }, []);

  return {
    updateNodePosition,
    releaseNode,
    reheatSimulation,
    nodes: simulationNodes,
    links: simulationLinks
  };
};