import React from 'react';
import { createRoot } from 'react-dom/client';
import { NetworkVisualizer } from './components/NetworkVisualizer';
import type { NetworkVisualizerProps } from './components/types';

// Global interface for Webflow integration
interface NetworkVisualizerGlobal {
  init: (selector: string, options?: Partial<NetworkVisualizerProps>) => void;
  version: string;
}

// Default configuration
const defaultOptions: NetworkVisualizerProps = {
  theme: 'saltlick',
  responsive: true,
  autoplay: true
};

// Initialize function for Webflow
const init = (selector: string, options: Partial<NetworkVisualizerProps> = {}) => {
  const container = document.querySelector(selector);
  
  if (!container) {
    console.error(`NetworkVisualizer: Container not found for selector "${selector}"`);
    return;
  }

  // Ensure container has proper styling
  if (container instanceof HTMLElement) {
    if (!container.style.width || container.style.width === 'auto') {
      container.style.width = '100%';
    }
    if (!container.style.height || container.style.height === 'auto') {
      container.style.height = '600px';
    }
    if (!container.style.position) {
      container.style.position = 'relative';
    }
  }

  const mergedOptions = { ...defaultOptions, ...options };
  
  // Create React root and render
  const root = createRoot(container);
  root.render(React.createElement(NetworkVisualizer, mergedOptions));
};

// Auto-initialize on DOM ready
const autoInit = () => {
  const containers = document.querySelectorAll('.network-visualizer, [data-network-visualizer]');
  
  containers.forEach((container) => {
    if (container instanceof HTMLElement) {
      const options: Partial<NetworkVisualizerProps> = {};
      
      // Read options from data attributes
      if (container.dataset.theme) {
        options.theme = container.dataset.theme as 'saltlick' | 'default';
      }
      if (container.dataset.responsive !== undefined) {
        options.responsive = container.dataset.responsive === 'true';
      }
      if (container.dataset.autoplay !== undefined) {
        options.autoplay = container.dataset.autoplay === 'true';
      }
      
      const selector = container.id ? `#${container.id}` : '.network-visualizer';
      init(selector, options);
    }
  });
};

// Create global object
const NetworkVisualizerGlobal: NetworkVisualizerGlobal = {
  init,
  version: '1.0.0'
};

// Attach to window for global access
declare global {
  interface Window {
    NetworkVisualizer: NetworkVisualizerGlobal;
  }
}

if (typeof window !== 'undefined') {
  window.NetworkVisualizer = NetworkVisualizerGlobal;
  
  // Auto-initialize on DOM content loaded
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', autoInit);
  } else {
    autoInit();
  }
}

export { NetworkVisualizer, init };
export default NetworkVisualizerGlobal;