import { useState, useEffect } from 'react';
import type { NetworkDimensions } from '../components/types';
import { theme } from '../styles/theme';

export const useResponsive = (containerRef: React.RefObject<HTMLElement | null>) => {
  const [dimensions, setDimensions] = useState<NetworkDimensions>({
    width: 0,
    height: 0,
    isMobile: false
  });

  useEffect(() => {
    const updateDimensions = () => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect();
        const width = rect.width || window.innerWidth;
        const height = rect.height || window.innerHeight;
        const isMobile = width < theme.breakpoints.mobile;

        setDimensions({
          width,
          height,
          isMobile
        });
      }
    };

    // Initial measurement
    updateDimensions();

    // Create ResizeObserver for container
    let resizeObserver: ResizeObserver | null = null;
    
    if (containerRef.current) {
      resizeObserver = new ResizeObserver(updateDimensions);
      resizeObserver.observe(containerRef.current);
    }

    // Fallback to window resize listener
    const handleWindowResize = () => {
      updateDimensions();
    };

    window.addEventListener('resize', handleWindowResize);

    return () => {
      if (resizeObserver) {
        resizeObserver.disconnect();
      }
      window.removeEventListener('resize', handleWindowResize);
    };
  }, [containerRef]);

  const getNodeSize = (tier: 'primary' | 'secondary' | 'tertiary') => {
    return dimensions.isMobile 
      ? theme.nodeSize.mobile[tier]
      : theme.nodeSize.desktop[tier];
  };

  const isTablet = dimensions.width >= theme.breakpoints.mobile && 
                   dimensions.width < theme.breakpoints.desktop;

  const isDesktop = dimensions.width >= theme.breakpoints.desktop;

  return {
    dimensions,
    getNodeSize,
    isMobile: dimensions.isMobile,
    isTablet,
    isDesktop
  };
};