import React, { useRef, useState, useCallback, useEffect, useMemo } from 'react';
import styled from 'styled-components';
import { TouchPointNode } from './TouchPointNode';
import { ElasticWebLine } from './ElasticWebLine';
import type { NetworkVisualizerProps, SimulationNode, SimulationLink } from './types';
import { useResponsive } from '../hooks/useResponsive';
import { useForceSimulation } from '../hooks/useForceSimulation';
import { TOUCHPOINTS } from '../data/touchpoints';
import { theme } from '../styles/theme';

const NetworkContainer = styled.div`
  width: 100%;
  height: 100%;
  min-height: 400px;
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, ${theme.colors.neutral} 0%, #151518 100%);
  border-radius: 8px;
  font-family: 'Roboto', sans-serif;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(
      circle at 30% 20%,
      ${theme.colors.primaryGlow} 0%,
      transparent 50%
    ),
    radial-gradient(
      circle at 70% 80%,
      ${theme.colors.secondaryGlow} 0%,
      transparent 50%
    );
    opacity: 0.2;
    pointer-events: none;
  }
`;


const NodesContainer = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 2;
`;

const LoadingOverlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: ${theme.colors.neutral}cc;
  color: ${theme.colors.inverse};
  font-family: ${theme.typography.secondary.family};
  font-weight: ${theme.typography.secondary.weight};
  z-index: 10;
`;

export const NetworkVisualizer: React.FC<NetworkVisualizerProps> = ({
  autoplay = true,
  className = ''
}) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const [hoveredNode, setHoveredNode] = useState<string | null>(null);
  const [draggedNode, setDraggedNode] = useState<string | null>(null);
  const [simulationNodes, setSimulationNodes] = useState<SimulationNode[]>([]);
  const [simulationLinks, setSimulationLinks] = useState<SimulationLink[]>([]);
  const [isInitialized, setIsInitialized] = useState(false);

  const { dimensions, getNodeSize } = useResponsive(containerRef);

  const handleTick = useCallback((nodes: SimulationNode[], links: SimulationLink[]) => {
    setSimulationNodes([...nodes]);
    setSimulationLinks([...links]);
    
    if (!isInitialized && nodes.some(n => n.x && n.y)) {
      setIsInitialized(true);
    }
  }, [isInitialized]);

  const {
    updateNodePosition,
    releaseNode,
    reheatSimulation
  } = useForceSimulation({
    nodes: TOUCHPOINTS,
    dimensions,
    onTick: handleTick
  });

  const handleNodeHover = useCallback((nodeId: string | null) => {
    setHoveredNode(nodeId);
  }, []);

  const handleDragStart = useCallback((nodeId: string) => {
    setDraggedNode(nodeId);
  }, []);

  // Throttle drag updates for better performance
  const throttleRef = useRef<{ [key: string]: number }>({});
  
  const handleDrag = useCallback((nodeId: string, x: number, y: number) => {
    const now = Date.now();
    const lastUpdate = throttleRef.current[nodeId] || 0;
    
    // Throttle to ~60fps (16ms)
    if (now - lastUpdate >= 16) {
      updateNodePosition(nodeId, x, y);
      throttleRef.current[nodeId] = now;
    }
  }, [updateNodePosition]);

  const handleDragEnd = useCallback((nodeId: string) => {
    setDraggedNode(null);
    releaseNode(nodeId);
  }, [releaseNode]);

  // Auto-reheat simulation periodically to maintain movement
  useEffect(() => {
    if (!autoplay) return;

    const interval = setInterval(() => {
      reheatSimulation();
    }, 10000); // Every 10 seconds

    return () => clearInterval(interval);
  }, [autoplay, reheatSimulation]);

  // Gentle web animation on hover - applies subtle force to connected nodes
  useEffect(() => {
    if (hoveredNode) {
      const timer = setTimeout(() => {
        // Apply gentler simulation heating for web-like movement
        reheatSimulation();
      }, 50);
      
      return () => clearTimeout(timer);
    }
  }, [hoveredNode, reheatSimulation]);

  // Get connected links for highlighting - memoized for performance
  const connectedLinks = useMemo(() => {
    if (!hoveredNode) return [];
    
    return simulationLinks.filter(link => {
      const source = typeof link.source === 'string' ? link.source : link.source.id;
      const target = typeof link.target === 'string' ? link.target : link.target.id;
      return source === hoveredNode || target === hoveredNode;
    });
  }, [hoveredNode, simulationLinks]);

  return (
    <NetworkContainer 
      ref={containerRef} 
      className={className}
      role="application"
      aria-label="Interactive Client Acquisition Network Visualizer"
    >
      {!isInitialized && (
        <LoadingOverlay>
          Initializing Network...
        </LoadingOverlay>
      )}
      
      {/* Elastic Web Lines */}
      {simulationLinks.map((link, index) => {
        const source = typeof link.source === 'string' ? link.source : link.source.id;
        const target = typeof link.target === 'string' ? link.target : link.target.id;
        const isHighlighted = connectedLinks.some(cl => {
          const clSource = typeof cl.source === 'string' ? cl.source : cl.source.id;
          const clTarget = typeof cl.target === 'string' ? cl.target : cl.target.id;
          return (clSource === source && clTarget === target) || 
                 (clSource === target && clTarget === source);
        });

        return (
          <ElasticWebLine
            key={`${source}-${target}-${index}`}
            link={link}
            isHighlighted={isHighlighted}
            getNodeSize={getNodeSize}
          />
        );
      })}

      {/* Touch Point Nodes */}
      <NodesContainer>
        {simulationNodes.map((node) => (
          <TouchPointNode
            key={node.id}
            node={node}
            nodeSize={getNodeSize(node.tier)}
            isHovered={hoveredNode === node.id}
            isDragging={draggedNode === node.id}
            onHover={handleNodeHover}
            onDragStart={handleDragStart}
            onDrag={handleDrag}
            onDragEnd={handleDragEnd}
          />
        ))}
      </NodesContainer>
    </NetworkContainer>
  );
};